import XInput from 'vux/src/components/x-input/index'
import OfoCarNumInput from './componnets/OfoCarNumInput'
import OfoErrorMsg from './componnets/OfoErrorMsg'
import CountDown from '../../../utils/CountDown'
const COUNT_DOWN_TIME = 30 // 倒计时时间,单位秒
export default {
  name: 'login-verify-code-screen',
  components: {XInput, OfoCarNumInput, OfoErrorMsg},
  data () {
    return {
      code: '',
      codeLength: 4,
      params: {},
      remain: COUNT_DOWN_TIME, // 剩余时间
      isLogin: !this.$route.params.email,
      countDown: null,
      errorMsg: ''
    }
  },
  methods: {
    onGoClick () {
      if (this.isLogin) this.login()
      else this.register()
    },
    // 用户登录
    async login () {
      const {code, params} = this
      try {
        this.$loading.show()
        const {OK, msg} = await this.$store.dispatch('login', {...params, code})
        this.$loading.hide()
        if (OK) {
          // 根据状态跳转登录后的页面
          const {to} = this.$route.query
          $app.goAndReplace({name: to || 'HomeScreen'}, -1)
        } else {
          this.errorMsg = msg
        }
      } catch (e) {
        this.$loading.hide()
        this.$toast.text(e)
      }
    },
    // 注册
    async register () {
      const {code, params} = this
      try {
        this.$loading.show()
        const {OK, msg} = await $API.ofo.register({...params, code})
        this.$loading.hide()
        if (OK) {
          this.$store.commit('setIsDeviceRegister', true)
          this.$router.go(-1)
          this.$toast.text('注册成功，请登录')
        } else {
          this.errorMsg = msg
        }
      } catch (e) {
        this.$loading.hide()
        this.$toast.text(e)
      }
    },
    // 重发验证码
    async sendAgain () {
      // 重新获取验证码
      try {
        this.$loading.show()
        this.errorMsg = ''
        const {OK, msg} = await $API.ofo.getVerifyCode({...this.params, type: this.isLogin ? 1 : 0})
        this.$loading.hide()
        if (OK) {
          // 重置倒计时
          this.remain = COUNT_DOWN_TIME
          this.countDown.totalTime = this.remain
          this.countDown.start()
        } else {
          this.errorMsg = msg
        }
      } catch (e) {
        this.$loading.hide()
        this.$toast.text(e)
      }
    }
  },
  mounted () {
    this.countDown = new CountDown(this.remain, (remain) => {
      remain = Math.round(remain)
      this.remain = remain < 0 ? 0 : remain
    })
    this.countDown.start()
  },
  created () {
    this.params = this.$route.params
  }
}



